      width: 100%;
      display: block;
      margin-bottom: 0.2rem;
      margin-top: 0.2rem;
    }
    p {
      font-size: 0.3rem;
      color: #666;
    }
  }
}
</style>
<style lang="css">
.content-top p {
  font-size: 0.3rem;
}
.content-desc img {
  max-width: 100%;
  height: auto;
  display: block;
  margin-bottom: 0.2rem;
}
.content-desc p {
  font-size: 0.3rem;
  line-height: 0.45rem;
  color: #666;
}
</style>


