<template>
  <m-container>
    <m-center :pageStatus="pageStatus">
      <div class="m-content" solt="swiper">
        <div class="m-banner animated bounceInUp">
          <swiper :options="swiperOption">
            <swiper-slide
              class="swiper-slide"
              v-for="(item,index) in slide"
              :key="index"
            >slide{{item}}</swiper-slide>
            <div class="swiper-pagination"  slot="pagination"></div>
          </swiper>
        </div>
        <h1 @click="get" class="font-24">{{ msg }}</h1>
        <p>{{$store.state.title}}</p>
        <span>{{$store.state.count}}</span>

        <button @click="$store.commit('add')">add</button>
        <button @click="$store.commit('reduce')">reduce</button>
        <div>{{count}}</div>
        <b>{{counts}}</b>
        <i>{{host}}</i>
        <button @click="add">add</button>
        <button @click="reduce">reduce</button>

        <button @click="$store.dispatch('addAction')">add</button>
        <button @click="reduceAction">reduce</button>

        <button @click="$store.dispatch('network',{onLine:false})">online:{{onLine}}</button>
      </div>
      <div class="wrapper" ref="wrapper">
        <ul class="content">
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>
      </div>
    </m-center>
  </m-container>
</template>

<script>
//import api from '@/plus/api';
import store from "@/store";
import { mapState, mapMutations, mapGetters, mapActions } from "vuex";
import config from "@/plus/http/config";
import BScroll from "better-scroll"
export default {
  name: "index",
  data() {
    return {
      pageStatus: 0,
      host: config.host,

      title: "",
      msg: "Welcome to Your Vue.js",
      filter: {
        activityKey: "kaixuji",
        effectId: 2,
        selectAredId: "" || "a2c11ad0-3f24-4543-9d22-140fd7b2bc00"
      },
      slide: [1, 2, 3, 4, 5],
      swiperOption: {
          pagination: '.swiper-pagination', //swiper-pagination
          loop: true  //
        }
    };
  },
  computed: {
    // count() {
    //   return store.state.count;
    // },
    ...mapState(["count", "onLine"]),
    ...mapGetters(["counts"])
    // counts(){
    //     return this.$store.getters.counts;
    // }
  },
  activated() {},
  mounted() {
    this.scroll = new BScroll(this.$refs.wrapper,{
      click: true,
      startX:20,
      startY:30,
      scrollX:true,
      scrollY:true,
      freeScroll:true,
      eventPassthrough:'horizontal',
      bounce:{top:true},
      pullDownRefresh:{
        threshold: 50,
        stop: 20
      },
      pullUpLoad:{
        threshold: 50
      },
    })
    this.scroll.on('',)
    this.pageStatus = 2;
    this.getData();
  },
  methods: {
    ...mapMutations(["add", "reduce"]),
    ...mapActions(["addAction", "reduceAction", "network_update"]),
    async get() {
      var filters = {
        activityKey: "kaixuji",
        effectId: 1,
        selectAredId: ""
      };
      await this.$http.schoolOpens
        .getSchoolOpensList(filters)
        .then(res => {
          console.log(res);
        })
        .catch(err => {
          console.log(err);
        });
    },
    async getData() {
      // await this.$http.home.getData().then(res => {
      //   console.log(res)
      // }).catch(err=>{
      //   console.log(err)
      // })
    }
  }
};
</script>

<style lang="less" scoped>

.wrapper{
    width: 100%;
    background:#ccc;
    height: 70vh;
    overflow: hidden;
    .content{
      width:500px
    }
}

.m-banner {
  width: 6.9rem;
  margin: 0.2rem auto;
  border-radius: 0.18rem;
  overflow: hidden;
}
.swiper-slide {
  height: 3.3rem;
  background: cadetblue;
  font-size: 50px;
  text-align: center;
  line-height: 3.3rem;
}

</style>



