<script>
//import api from '@/plus/api';
import store from "@/store";
import config from "@/plus/http/config";
export default {
  name: "project.detail",
  data() {
    return {
      pageStatus: 0,
      host: config.host,
      navIndex:0,
      //nav
      swiperOptionNav: {
        slidesPerView: 'auto',
        spaceBetween: 30,
        freeMode: false,
        initialSlide: 0,
      },
      //tab
      swiperOptionTab: {
        spaceBetween: 20,
        autoHeight: true,
        slidesPerView: 1,
        initialSlide: 0,
        on:{
          slideChange: ()=>{
            let swiperThumbs = this.$refs.swiperTab.swiper;
            this.navIndex = swiperThumbs.activeIndex;
          }
        },
      },

      //
      tapRelative:0,


      zsId: "",
      isShowAll: false, //
      detailData: void 0, //

      //
      showLength:2
    };
  },
  computed: {
    swiperTop(){
      return this.$refs.swiperNav.swiper;
    },
    swiperThumbs(){
      return this.$refs.swiperTab.swiper;
    },
  },
  created() {
    var id = this.$route.params.id;
    this.zsId = id;
    this.getDetail();
  },
  activated() {},
  deactivated() {},
  mounted() {
    this.swiper();
  },
  filters: {
    viewCount(value) {
      value = parseInt(value * 0.3);
      return value;
    },
    Hanzi(value){
      value = value.replace(/[^\u4e00-\u9fa5]/g, '');
      return value;
    },
    //
    timeFormat(time) {
      var _this = this;
      var timeTip;
      time = (typeof time === 'number') ? new Date(time) : new Date((time).replace(/-/g, '/'));
      var nowTime = new Date(); //
      var timeDifference = time.getTime() - nowTime.getTime(); //
      var day = 0,
        hour = 0,
        minute = 0,
        second = 0;
      if (timeDifference < 0) {
        timeDifference = Math.abs(timeDifference);
        day = Math.floor(timeDifference / 1000 / 60 / 60 / 24);
        hour = Math.floor((timeDifference / 1000 / 60 / 60) % 24);
        minute = Math.floor((timeDifference / 1000 / 60) % 60);
        second = Math.floor((timeDifference / 1000) % 60);
        timeTip =
          day > 0
            ? day + ""
            : hour > 0
            ? hour + ""
            : minute > 0
            ? minute + ""
            : second > 0
            ? second + ""
            : "";
        return timeTip;
      } else {
        day = Math.floor(timeDifference / 1000 / 60 / 60 / 24);
        hour = Math.floor((timeDifference / 1000 / 60 / 60) % 24);
        minute = Math.floor((timeDifference / 1000 / 60) % 60);
        second = Math.floor((timeDifference / 1000) % 60);
        timeTip =
          day > 0
            ? day + ""
            : hour > 0
            ? hour + ""
            : minute > 0
            ? minute + ""
            : second > 0
            ? second + ""
            : "";
        return timeTip;
      }
    },
  },
  methods: {
    //swiper-
    slidTo(index){
      event.preventDefault();
      this.navIndex = index;
      this.swiperThumbs.slideTo(this.navIndex, 200, false);
    },
    swiper(){
      this.$nextTick(() => {
        // this.swiperTop.controller.control = this.swiperThumbs
        this.swiperThumbs.controller.control = this.swiperTop
      })
      this.swiperThumbs.on('transitionEnd',() => {
        //this.swiperThumbs.slideTo(this.navIndex, 100, false);
        this.swiperThumbs.activeIndex = this.navIndex;
      })
    },
    //
    async getDetail() {
      var _this = this;
      var model = {
        stateShowId: _this.zsId
      };
      await _this.$http.question
        .getQuestionDetail(model)
        .then(res => {
          if (res.data.Success) {
            _this.pageStatus = 2;
            _this.detailData = res.data.Content;
          }else{
            _this.$toast.center(res.data.Message);
            // setTimeout(()=>{
            //   _this.$router.go(-1);
            //   location.reload();
            // },1000)
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>

