            &::after{
              content:"";
              position:absolute;
              bottom:-.1rem;
              left:0;
              width:100%;
              height:.06rem;
              border-radius: .08rem;
              background:#ff8686;
            }
          }
        }
      }
    }
    .swiper-tab{
      .swiper-slide{
        border-radius: .16rem;
        box-shadow: 0 1px 10px 0 rgba(204, 204, 204, 0.5), 0 1px 8px 0 rgba(204, 204, 204, 0.2);
        overflow: hidden;
        padding:.3rem;
        transition: 300ms;
        transform: scale(0.9);
        &.swiper-slide-active{
          transform: scale(1);
        }
        .learn-more{
          display:block;
          padding:.1rem .2rem;
          border-radius: 1rem;
          background:#f5f5f5;
          text-align: center;
        }
      }
    }
  }
  
}
//
.relative-content{
  .title-box{
    .title{
      flex:1;
      flex-shrink: 0;
      text-align: center;
      position:relative;
      padding-bottom:.1rem;
      &:after{
        content:"";
        position:absolute;
        top:0;
        right:0;
        width:1px;
        height:100%;
        background:#f0f0f0;
      }
      &:last-child:after{
        background:transparent;
      }
      &.active{
        font-weight: bold;
        color:#000;
        position: relative;
        &:before{
          content:"";
          position: absolute;
          left:50%;
          bottom:0;
          width:.6rem;
          height:.08rem;
          margin-left:-.3rem;
          border-radius: .08rem;
          background:#ff8686;
        }
      }
    }
  }
  .content-boxs{
    .relative-list{
      .border-bottom{
        border-bottom:1px solid #f0f0f0;
      }
      .content-item{
        overflow: hidden;
        .content-item-left{
          width:100%;
        }
      }
    }
  }
}
//
.hospital{
  .hospital-head{
    width:1.28rem;
    height:1.28rem;
    border-radius: .08rem;
    object-fit:cover;
  }
  .hospital-info{
    width:5.3rem;
    height:1.28rem;
    margin-left:.3rem;
    p{
      width:100%;
    }
  }
}
// 
.look-more{
  img{
    width:.4rem;
    height:.4rem;
    margin-right:.12rem;
  }
}
</style>
<style lang="css">
.content-wrap p{
  font-size: 0.3rem;
  color: #666;
  line-height: .48rem;
}
.content-wrap p span{
  font-size: 0.3rem;
  color: #666;
  line-height: .48rem;
}
.content-wrap img{
  margin:.1rem 0;
}
.v-html,.v-html p,.v-html span{
  font-size:inherit;
  color:#666;
  line-height: .45rem;
}

</style>
<style lang="css" scoped>

</style>


